






























































import { IPointsItem } from '@/types/interface';
import { Component, Vue } from 'vue-property-decorator';
import { Action, State } from 'vuex-class';
import banner from '../../components/Banner.vue';
import pointsLogItem from './PointsLogItem.vue';

@Component({
  components: {
    banner,
    pointsLogItem,
  },
})

export default class PointsRecord extends Vue {
  @Action('getMemberBalance', { namespace: 'task' }) getMemberBalance!: () => void;
  @Action('getMemberPayDetail', { namespace: 'task' }) getMemberPayDetail!: () => void;
  @State('points', { namespace: 'task' }) statePoints!: IPointsItem[];

  // points = 0;
  flag = 'new';
  showFixed = false;
  top = 0;

  // newList = [];

  get newList() {
    return this.statePoints;
  }

  get points() {
    return this.newList.length === 0 ? 0 : this.newList.map((d: IPointsItem) => d.points).reduce((sum: number, d: number) => sum + d);
  }

  usedList = [];

  get showEmpty() {
    return this.flag === 'new' ? this.newList.length === 0 : this.usedList.length === 0;
  }

  get showList() {
    return this.flag === 'new' ? this.newList : this.usedList;
  }

  created() {
    this.getMemberBalance();
    this.getMemberPayDetail();
  }

  link() {
    this.$router.push('/activity/main');
  }

  expire() {
    this.$router.push('/activity/expired_points');
  }

  changeFlag(opt: string) {
    if (opt !== this.flag) {
      this.flag = opt;
      const temp = document.querySelector('.main') as HTMLElement;
      temp.scrollTop = this.top;
    }
  }

  handleScroll(e: any) {
    this.top = e.target.scrollTop;
    this.showFixed = (this.top > 228);
  }
}
